body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/* *{
  border: 0.4px solid red;
} */

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
nav{
  padding: 1rem;
  color: aliceblue;
  background-color: rgb(0, 54, 85);
}
.name{
  color: rgb(237, 251, 246);
font-size: xx-large;
font-weight: bolder;
}


.jobName{
  color: rgb(0, 151, 98);
font-size: x-large;
font-weight: bolder;
}
.seactionHeader{
color: rgb(0, 141, 94);
font-size: x-large;
font-weight: bolder;
text-decoration-line: underline;
}

.miniHeader{
  margin: 10px 0px 10px  0px;
  color: rgb(1, 50, 34);

}
.mainSection{
  display: flex;
}
.mainSection div{
  width: 100%;
  padding: 1rem;
}


.Banner{
  margin: 0px auto;
  display: grid;
  color: aliceblue;
  gap: 10px;
  background-color: rgb(3, 71, 110);

  grid-template-columns: auto auto auto;
  padding: 10px;

}

.Banner div{
 display: flex;
 gap: 10px;

}
a {
  color: inherit;
  text-decoration: inherit;
  cursor: inherit;
}